.sidebar {
    height: 100vh;
    background-color: $dim-white; }

.App-logo {
    color: $dark-blue;
    font-weight: bold;
    font-size: 1.2rem;

    &--holder {} }

// Navigation
//--------------------
.nav {
    padding-top: 6rem;
    color: $dark-grey;

    &-item {
        display: block;

        a {
            color: $secondary-color; }

        &.active {
            font-weight: bold;
            color: $dark-blue; }

        &:hover {
            .nav-title {
                box-shadow: 0 2px 0 $secondary-color;
                color: $secondary-color;
                transition: 0.6s all ease; } } }

    &-icon {

        &--holder {
            display: inline-block; } }

    &-title {
        display: inline-block;
        margin-left: 0.4rem; } }


