@import url(https://fonts.googleapis.com/css?family=Open+Sans|Space+Mono:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Space Mono", monospace; }

.container-fluid {
  overflow: hidden; }

a {
  color: default; }
  a:hover {
    text-decoration: none; }

p {
  margin-bottom: 0; }

.sep-large {
  display: block;
  padding-bottom: 5rem; }

.sep-medium {
  display: block;
  padding-bottom: 3rem; }

.sep-small {
  display: block;
  padding-bottom: 1rem; }

.code-text {
  color: #C7C6CA;
  font-size: 0.8rem; }

h1 {
  font-weight: bold;
  color: #333161;
  font-size: 2.8rem; }

.main-section {
  padding-top: 7.7rem;
  padding-left: 2rem; }

.description-text {
  color: #73717C;
  font-size: 1.2rem; }
  .description-text span {
    font-size: 1rem;
    display: block; }

.button {
  padding: 1rem 3rem;
  margin-right: 1rem;
  border: 2px solid #44E291;
  text-align: center;
  font-weight: bold;
  font-size: 1.4rem; }
  .button:hover {
    background-color: #333161;
    border-color: #333161;
    color: #ffffff;
    box-shadow: 10px 10px 0 #44E291;
    -webkit-transition: all 0.6s ease;
    transition: all 0.6s ease; }
  .button--container {
    float: left; }
    .button--container a {
      display: inline-block; }
  .button-primary {
    background-color: #44E291;
    color: #333161;
    border: 2px solid #44E291; }
  .button-secondary {
    color: #44E291; }
    .button-secondary:hover {
      background-color: #44E291;
      border-color: #44E291;
      color: #333161;
      box-shadow: 10px 10px 0 #333161; }
  .button-disabled {
    border: none; }
    .button-disabled:hover {
      box-shadow: 0px 0px 0 #ffffff;
      background-color: #C7C6CA; }

footer {
  padding: 0.1rem;
  font-size: 0.72727rem;
  background-color: #F4F5F7;
  color: #73717C;
  padding-right: 2rem; }

.description-text ul li {
  padding-top: 0.4rem; }

.work-url {
  color: #4D4A58;
  text-decoration: underline; }
  .work-url:hover {
    color: #44E291;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease; }

.company-url:hover {
  color: #44E291;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.sidebar {
  height: 100vh;
  background-color: #F4F5F7; }

.App-logo {
  color: #333161;
  font-weight: bold;
  font-size: 1.2rem; }

.nav {
  padding-top: 6rem;
  color: #73717C; }
  .nav-item {
    display: block; }
    .nav-item a {
      color: #333161; }
    .nav-item.active {
      font-weight: bold;
      color: #333161; }
    .nav-item:hover .nav-title {
      box-shadow: 0 2px 0 #333161;
      color: #333161;
      -webkit-transition: 0.6s all ease;
      transition: 0.6s all ease; }
  .nav-icon--holder {
    display: inline-block; }
  .nav-title {
    display: inline-block;
    margin-left: 0.4rem; }

.cta-buttons {
  margin-top: 1.4rem; }

.social-icon {
  margin-right: 1.2rem;
  height: 2.8rem; }
  .social-icons--holder {
    margin-top: 1rem; }

.img-bg {
  background-size: 44%;
  background-repeat: no-repeat;
  background-position: right bottom 1rem; }
  .img-bg.front-page {
    background-image: url(/portfolio-gmrsagar/static/media/hero-illustration.c2564997.svg); }
  .img-bg.about-page {
    background-size: 24%;
    background-position: right 6rem top 8rem;
    background-image: url(/portfolio-gmrsagar/static/media/hero-illustration3.e9fde07c.svg); }
  .img-bg.contact-page {
    background-image: url(/portfolio-gmrsagar/static/media/hero-illustration2.93e80dea.svg); }

.project-card {
  border: 1px solid #DBDDE1;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 2rem;
  background-color: #ffffff; }
  .project-card-info {
    padding: 1.2rem 1.5rem; }
  .project-card-description {
    color: #73717C;
    font-size: 0.9rem;
    line-height: 1.2rem;
    font-family: "Open Sans", sans-serif; }
  .project-card-image {
    width: 100%; }
  .project-card-title {
    font-weight: bold;
    font-size: 1.4rem;
    color: #333161; }

.project-tech {
  font-size: 0.8rem;
  color: #73717C; }
  .project-tech--holder {
    margin-top: 0.4rem; }
  .project-tech::before {
    width: 10px;
    border-radius: 20px;
    content: '';
    display: inline-block;
    height: 10px;
    background-color: #FFDA83;
    margin-right: 0.2rem; }

.button-nobg {
  color: #44E291;
  display: inline-block;
  font-size: 0.9rem; }
  .button-nobg:hover {
    color: #333161;
    font-weight: bold;
    box-shadow: 0 2px 0 #333161;
    -webkit-transition: 0.6s all ease;
    transition: 0.6s all ease; }
  .button-nobg--holder {
    margin-top: 1.1rem;
    display: block; }

.contact-form input, .contact-form textarea {
  display: block;
  width: 28rem;
  height: 3rem;
  padding-left: 1rem;
  border: 1px solid #C7C6CA; }

.contact-form textarea {
  padding-top: 0.6rem;
  height: 6rem;
  resize: none; }

.contact-form label {
  font-size: 1.4rem;
  font-weight: bold;
  color: #333161; }

